import * as Yup from "yup";

export const FiltrationSchema = Yup.object().shape({
  minPrice: Yup.number()
    .typeError("  ")
    .min(0, "    0")
    .max(1000000, "  ")
    .notRequired(),
  maxPrice: Yup.number()
    .typeError("  ")
    .min(0, "    0")
    .max(1000000, "  ,    !")
    .notRequired(),
});
